/*-- Fonts --*/


@font-face {
  font-family: 'SpaceQuest';
  src: url("assets/fonts/SpaceQuest.ttf");
}

@font-face {
  font-family: 'SpaceQuest';
  font-style: italic;
  src: url("assets/fonts/SpaceQuest-Italic.ttf");
}

@font-face {
  font-family: 'Coven';
  src: url("assets/fonts/Coven.ttf");
}

@font-face {
